.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100vh;
    background: linear-gradient(orange, red, yellow);
  }
  
  .header {
    padding-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
  }
  
  .logo {
    width: 230px;
    height: 230px;
    object-fit: contain;
  }
  
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .text {
    font-size: 20px;
  }
  
  .button {
    background-color: green;
    padding: 10px;
    border-radius: 5px;
    margin-top: 20px;
    cursor: pointer;
  }
  
  .buttonText {
    color: white;
    font-size: 16px;
  }
  
  .disabledButton {
    background-color: gray;
    cursor: not-allowed;
  }
  
  .imageContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .randomImage {
    width: 250px;
    height: 250px;
    object-fit: cover;
    margin-top: 20px;
    border: none;
    border-radius: 10px;
  }
  
  .randomText {
    font-size: 24px;
    margin-top: 10px;
    font-weight: bold;
    color: white;
  }

  .randomNumber {
    font-size: 24px;
    margin-top: 30px;
    font-weight: bold;
    color: white;
  }

  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  .rotate {
    margin-top: 40px;
    animation: spin 2s linear infinite;
  }
  