/* notificacoes.css */

.notificacoes {
  background-color: black;
  color: white;
  text-align: center;
  font-weight: bold;
  font-size: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.notificacoes img {
  max-width: 100%;
  height: auto;
}

.notificacoes p {
  margin: 10px 0;
}

.notificacoes button {
  margin-top: 20px;
  margin-bottom: 50px;
  padding: 10px 20px;
  background-color: rgb(0, 125, 6);
  color: black;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

/* Estilos adicionais conforme necessário */
