/* produtos.css */
.produtos-container {
  position: relative;
  width: 100%;
  height: 100vh; /* Torna o container 100% da altura da tela */
  overflow: hidden; /* Oculta a barra de rolagem, se houver */
}

.iframe-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* Faz o iframe ocupar toda a tela */
  border: none; /* Remove a borda padrão do iframe */
}
